import { SplideSlide } from "@splidejs/react-splide";
import React, { useRef, useState } from "react";
import { Card } from "react-bootstrap";
import SlideSection from "./common/slideSection";
import SectionHeading from "../../analytics-new/layout/common/sectionHeading";
import AuthorInfo from "../../analytics-new/content/components/authorInfo";
import NetworkIcon from "../../analytics-new/layout/common/networkIcon";
import PostMedia from "../../analytics-new/layout/common/postMedia";
import { useDispatch } from "react-redux";
import { postStatusDashboard } from "../../../../actions/display/moderationActions";
import RecentpostSkeleton from "./skeleton/recentpostSkeleton";
import { useNavigate } from "react-router-dom";
import Sentiment from "./common/sentiment";

    const RecentPost = (props) => {

    const splideRef = useRef(null);
    const dispatch = useDispatch()
    const navigate = useNavigate();
    const [activeWall, setactiveWall] = useState(0);
    const { recentPosts, loader, getNotRecomendedList, isUpgrade } = props

    const postStatus=(index, item, value)=>{
        dispatch(postStatusDashboard(index, item, value));
    }

    const redirect =(e)=>{
        navigate(`/moderation/index/${e}`); localStorage.setItem("wallId", e)
    }
    
  
    return (
       (recentPosts && Object.keys(recentPosts).length) || loader ? 
        <div className='recent-post-section mb-5'>
        <Card className='p-0 card-custom card-stretch'>
        <SectionHeading title={'Recent Posts'} total={0} section={2} isDashboard={'card-title h5 w-100'} />
        <Card.Body className="py-5 px-0 ">
                

       { loader ? <RecentpostSkeleton/> : 
       
      <SlideSection setactiveWall={setactiveWall} section={2} totalRecord={recentPosts.length}  splideRef={splideRef}>    
 
       { recentPosts.map((e,index)=>{    
         return(
         <SplideSlide  key={index}>    
             <div onLoad={isUpgrade ? getNotRecomendedList(e) : null} className='p-5 d-flex h-100 cursor-pointer'>
                <div className='p-5 border rounded w-100'>
                    <div className='row h-100'>
                        <div  key={index} onClick={() => redirect(e.wallId) } className='col-sm-5'>
                            <div className='home_recentPost text-center h-100'>
                                <PostMedia
                                    isSplide={true}
                                    postFile={e.postFile}
                                    content={e.content}
                                    mediaType={e.type}
                                    postId={e.id}
                                    className={"img-fluid"}
                                />
                            </div>
                        </div>
                        <div className='col-sm-7'>
                            <div className='d-flex flex-column w-100 h-100 justify-content-between home_recentPost-content'>
                                <div key={index}  onClick={() => redirect(e.wallId) } className="w-100">
                                    <div className='d-flex align-items-center justify-content-between mb-5 home_recentPost_auther'>
                                        <AuthorInfo
                                            isSplide={true}
                                            picture={e.newPicture}
                                            authorUsername={e.username}
                                            authorName={e.name}
                                            date={e.created}
                                            networkId={e.social.id}
                                        />
                                        <div class="list_category_icon cursor-pointer pt-1">
                                            <NetworkIcon isDashboard={'font-size-h2'} icon={e.social.icon}/>
                                        </div> 
                                    </div>
                                    <div className='mb-5'>
                                        <p className="line-clamp-3 overflow-hidden text-dark">
                                            {e.content} 
                                        </p>
                                    </div>
                                </div>
                                <div className="w-100">
                                    <div onClick={() => redirect(e.wallId) } className='mb-5'>
                                     { isUpgrade ?   
                                        <div class="d-flex align-items-center justify-content-between p-3 mb-3 border rounded">
                                            <span class="text-dark font-weight-bold font-size-md">Sentiment</span>
                                            <p class="text-dark font-weight-boldest mb-0 d-flex">
                                              <Sentiment sentiment={e.sentiment} />     
                                            </p>
                                        </div> : null }
                                        <div class="d-flex align-items-center justify-content-between p-3 mb-3 border rounded">
                                            <span class="text-dark font-weight-bold font-size-md">Wall Name</span>
                                            <p class="text-dark font-weight-boldest mb-0 w-50 overflow-hidden text-nowrap text-ellipsis text-end">{ e.wall.name }</p>
                                        </div>
                                    </div>
                                    <div class="m_bottom_actions border-0">
                                        <ul class="list-inline mb-0 d-flex p-0">
                                            <li class="w-50 me-3">
                                                <button id={`approveBtn${e.id}`} onClick={()=> e.postStatus != 1 ? postStatus(index, e, 1) : null} 
                                                type="button" class={`btn m_grid_check ${e.postStatus == 1 ? 'm_active disabled' : ''} init`}>Public</button>
                                            </li>
                                            <li class="w-50">
                                                <button id={`rejectBtn${e.id}`} onClick={()=> e.postStatus != 2 ? postStatus(index, e, 2) : null}
                                                type="button" class={`btn m_grid_non_check  ${e.postStatus == 2 ? 'm_active disabled' : ''} init`}>Private</button>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
             </div>
         </SplideSlide> 
            )}
          )
        }

    </SlideSection>
    }
          </Card.Body>
        </Card>
    </div> : null 
    );
 };

 export default React.memo(RecentPost)